/**
 * 
 *
 * mm/dd~mm/dd 
 *  mm/dd 
 */
/**
 *  mm/dd 
 * @param dateStr - ISO8601 (: "2025-12-25")
 * @returns mm/dd  (: "12/25")
 */
function formatToMonthDay(dateStr) {
    const date = new Date(dateStr);
    const month = date.getMonth() + 1; // 0-indexed  +1
    const day = date.getDate();
    return `${month}/${day}`;
}
/**
 *  ()
 * @param dateStr - ISO8601 (: "2025-12-25")
 * @returns  (: "")
 */
function getDayOfWeek(dateStr) {
    const date = new Date(dateStr);
    const daysOfWeek = ['', '', '', '', '', '', ''];
    return daysOfWeek[date.getDay()];
}
/**
 *  mm/dd  (Version)
 *
 * @param date - ISO8601 or null
 * @returns mm/dd  null
 *
 * @example
 * formatDate("2025-12-25")  // "12/25"
 * formatDate(null)          // null
 * formatDate("")            // null
 */
export function formatDate(date) {
    if (!date || date === '') {
        return null;
    }
    return formatToMonthDay(date);
}
/**
 *  mm/dd()  (Version)
 *
 * @param date - ISO8601 or null
 * @returns mm/dd()  null
 *
 * @example
 * formatDateWithDayOfWeek("2025-01-15")  // "1/15()"
 * formatDateWithDayOfWeek(null)          // null
 * formatDateWithDayOfWeek("")            // null
 */
export function formatDateWithDayOfWeek(date) {
    if (!date || date === '') {
        return null;
    }
    const formattedDate = formatToMonthDay(date);
    const dayOfWeek = getDayOfWeek(date);
    return `${formattedDate}(${dayOfWeek})`;
}
/**
 *  "mm/dd~mm/dd" 
 *
 * @param startDate -  (ISO8601 or null)
 * @param dueDate -  (ISO8601 or null)
 * @returns 
 *
 * @example
 * formatDateRange("2025-12-20", "2025-12-25") // "12/20~12/25"
 * formatDateRange(null, "2025-12-25")         // "~12/25"
 * formatDateRange("2025-12-20", null)         // "12/20~"
 * formatDateRange(null, null)                 // null
 * formatDateRange("2024-12-28", "2025-01-05") // "12/28~1/5" ( mm/dd)
 */
export function formatDateRange(startDate, dueDate) {
    const hasStart = startDate != null && startDate !== '';
    const hasDue = dueDate != null && dueDate !== '';
    if (!hasStart && !hasDue) {
        return null;
    }
    const startStr = hasStart ? formatToMonthDay(startDate) : '';
    const dueStr = hasDue ? formatToMonthDay(dueDate) : '';
    if (hasStart && hasDue) {
        return `${startStr}~${dueStr}`;
    }
    else if (hasDue) {
        return `~${dueStr}`;
    }
    else {
        return `${startStr}~`;
    }
}
/**
 * 
 *
 * @param dueDate - ISO8601 or null
 * @returns truefalse
 *
 * @example
 * isOverdue("2025-01-01")  // true ()
 * isOverdue("2099-12-31")  // false ()
 * isOverdue(null)          // false ()
 * isOverdue("")            // false ()
 */
export function isOverdue(dueDate) {
    if (!dueDate || dueDate === '') {
        return false;
    }
    const today = new Date();
    today.setHours(0, 0, 0, 0); // 
    const due = new Date(dueDate);
    due.setHours(0, 0, 0, 0);
    return due < today;
}
/**
 * 
 *
 * @param dueDate - ISO8601 or null
 * @returns null
 *
 * @example
 * getDaysRemaining("2025-01-20")  // 15 (2025-01-05)
 * getDaysRemaining("2025-01-01")  // -4 (2025-01-05)
 * getDaysRemaining(null)          // null ()
 * getDaysRemaining("")            // null ()
 */
export function getDaysRemaining(dueDate) {
    if (!dueDate || dueDate === '') {
        return null;
    }
    const today = new Date();
    today.setHours(0, 0, 0, 0); // 
    const due = new Date(dueDate);
    due.setHours(0, 0, 0, 0);
    const diffTime = due.getTime() - today.getTime();
    const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
    return diffDays;
}
