import React, { useEffect, useRef, useState } from 'react';
export const Dropdown = ({ trigger, children, align = 'end', className = '' }) => {
    const [isOpen, setIsOpen] = useState(false);
    const dropdownRef = useRef(null);
    // 
    useEffect(() => {
        if (!isOpen)
            return;
        const handleClickOutside = (event) => {
            if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
                setIsOpen(false);
            }
        };
        const handleEscape = (event) => {
            if (event.key === 'Escape') {
                setIsOpen(false);
            }
        };
        document.addEventListener('mousedown', handleClickOutside);
        document.addEventListener('keydown', handleEscape);
        return () => {
            document.removeEventListener('mousedown', handleClickOutside);
            document.removeEventListener('keydown', handleEscape);
        };
    }, [isOpen]);
    return (React.createElement("div", { className: `dropdown ${className}`, ref: dropdownRef },
        React.createElement("div", { className: "dropdown-trigger", onClick: () => setIsOpen(!isOpen) }, trigger),
        isOpen && (React.createElement("div", { className: `dropdown-content dropdown-align-${align}` }, children))));
};
export const DropdownSection = ({ children }) => {
    return React.createElement("div", { className: "dropdown-section" }, children);
};
export const DropdownLabel = ({ children }) => {
    return React.createElement("div", { className: "dropdown-label" }, children);
};
export const DropdownSeparator = () => {
    return React.createElement("div", { className: "dropdown-separator" });
};
