/**
 * Ruby
 * 
 *
 * @example
 * naturalSortKey("10_") // => [[0, 10], [1, "_"]]
 * naturalSortKey("10_test_20_final") // => [[0, 10], [1, "_test_"], [0, 20], [1, "_final"]]
 *
 * @param str - 
 * @returns [type, value]type0=1=
 */
export function naturalSortKey(str) {
    const parts = str.match(/(\d+|\D+)/g) || [];
    return parts.map(part => {
        if (/^\d+$/.test(part)) {
            return [0, parseInt(part, 10)];
        }
        else {
            return [1, part];
        }
    });
}
/**
 * 
 * naturalSortKey
 *
 * @example
 * const keys = [
 *   naturalSortKey("100_"),
 *   naturalSortKey("10_"),
 *   naturalSortKey("2_")
 * ];
 * keys.sort(compareNaturalSort);
 * // => ["2_", "10_", "100_"]
 *
 * @param a - A
 * @param b - B
 * @returns -1, 0, 1
 */
export function compareNaturalSort(a, b) {
    const minLength = Math.min(a.length, b.length);
    for (let i = 0; i < minLength; i++) {
        const [typeA, valueA] = a[i];
        const [typeB, valueB] = b[i];
        // Type flag=0=1
        if (typeA !== typeB) {
            return typeA - typeB;
        }
        // 
        if (valueA < valueB)
            return -1;
        if (valueA > valueB)
            return 1;
    }
    // 
    return a.length - b.length;
}
