,wBACA,CAAA,qCAGF,wBACE,CAAA,eACA,CAAA,UACA,CAAA,QACA,CAAA,oBACA,CAAA,wBACA,CAAA,sCAGF,eACE,CAAA,SACA,CAAA,QACA,CAAA,yCAEA,uBACE,CAAA,wBACA,CAAA,UACA,CAAA,iDAEA,YACE,CAAA,mCACA,CAAA,gBACA,CAAA,0BACA,CAAA,mCAON,wBACE,CAAA,UACA,CAAA,QACA,CAAA,2BAQN,QACE,8BAEE,CAAA,eACA,CAAA,QAEF,wBAEE,CAAA,8DACA,CAAA,CAAA,2BAKJ,GACE,8BACE,CAAA,eACA,CAAA,8BACA,CAAA,GAGF,wBACE,CAAA,gCACA,CAAA,wBACA,CAAA,IAGF,wBACE,CAAA,0CACA,CAAA,wBACA,CAAA,IAGF,wBACE,CAAA,gCACA,CAAA,wBACA,CAAA,IAGF,wBACE,CAAA,0CACA,CAAA,wBACA,CAAA,IAGF,wBACE,CAAA,gCACA,CAAA,wBACA,CAAA,IAGF,wBACE,CAAA,0CACA,CAAA,wBACA,CAAA,IAGF,qCACE,CAAA,0CACA,CAAA,qCACA,CAAA,KAGF,8BACE,CAAA,eACA,CAAA,8BACA,CAAA,CAAA,kCAMJ,QACE,uCAEE,CAAA,eACA,CAAA,QAEF,sCAEE,CAAA,+DACA,CAAA,IAEF,gDACE,CAAA,+DACA,CAAA","sourcesContent":["// ========================================\n// CSS\n// ----------------------------------------\n//  @vibes/rules/css_standards.md \n// ========================================\n\n// ========================================\n// CSS Variables (px)\n// ========================================\n:root {\n  // Grid widths\n  --epic-width: 9.375rem;\n  --feature-width: 12.5rem;\n  --version-width: 18.75rem;\n\n  // Font sizes (20% increased, rem)\n  --font-feature-header: 0.975rem;\n  --font-user-story-header: 0.825rem;\n  --font-task-header: 0.675rem;\n  --font-task-item: 0.75rem;\n\n  // Padding (rem)\n  --feature-padding: 0.9rem;\n  --user-story-padding: 0.6rem;\n  --task-container-padding: 0.375rem;\n  --task-item-padding-v: 0.3rem;\n  --task-item-padding-h: 0.45rem;\n\n  // Gaps\n  --feature-gap: 0.6rem;\n  --user-story-gap: 0.6rem;\n  --task-container-gap: 0.375rem;\n  --task-item-gap: 0.225rem;\n\n  // Border radius\n  --border-radius-lg: 0.25rem;\n  --border-radius-md: 0.1875rem;\n  --border-radius-sm: 0.125rem;\n\n  // Spacing\n  --space-xs: 0.3125rem;\n  --space-sm: 0.5rem;\n  --space-md: 0.625rem;\n  --space-lg: 1.25rem;\n  --space-xl: 0.9375rem;\n\n  // Text overflow behavior (: )\n  --title-white-space: normal;\n  --title-text-overflow: clip;\n  --title-word-break: break-word;\n\n  // Triple Split Layout\n  --triple-split-min-left-width: 9.375rem; // 150px\n  --triple-split-max-left-width: 25rem; // 400px\n  --triple-split-min-center-width: 37.5rem; // 600px\n  --triple-split-min-right-width: 12.5rem; // 200px\n  --triple-split-max-right-width: 50rem; // 800px\n  --triple-split-default-left-width: 14.375rem; // 230px\n  --triple-split-default-right-width: 28.125rem; // 450px\n  --triple-split-splitter-width: 0.25rem; // 4px\n  --triple-split-splitter-handle-width: 2rem; // 32px\n  --triple-split-splitter-handle-height: 4rem; // 64px\n\n  // Side Panel\n  --side-panel-header-padding: 0.75rem 1rem; // 12px 16px\n  --side-panel-content-padding: 1rem; // 16px\n  --side-panel-footer-padding: 0.75rem 1rem; // 12px 16px\n  --side-panel-title-font-size: 1rem; // 16px\n  --side-panel-close-button-font-size: 1.25rem; // 20px\n  --side-panel-close-button-padding: 0.25rem 0.5rem; // 4px 8px\n  --side-panel-placeholder-font-size: 1.5rem; // 24px\n  --side-panel-placeholder-padding: 1.5rem 0; // 24px 0\n  --side-panel-list-item-padding: 0.5rem 0; // 8px 0\n  --side-panel-list-item-font-size: 0.875rem; // 14px\n  --side-panel-toggle-button-padding: 0.5rem 1rem; // 8px 16px\n  --side-panel-toggle-button-font-size: 0.875rem; // 14px\n\n  // Border widths\n  --border-width-thin: 0.0625rem; // 1px\n  --border-width-normal: 0.125rem; // 2px\n  --border-width-medium: 0.1875rem; // 3px\n  --border-width-thick: 0.25rem; // 4px\n\n  // Common sizes (rem)\n  --size-2: 0.125rem; // 2px\n  --size-3: 0.1875rem; // 3px\n  --size-4: 0.25rem; // 4px\n  --size-6: 0.375rem; // 6px\n  --size-8: 0.5rem; // 8px\n  --size-12: 0.75rem; // 12px\n  --size-13: 0.8125rem; // 13px\n  --size-14: 0.875rem; // 14px\n  --size-16: 1rem; // 16px\n  --size-18: 1.125rem; // 18px\n  --size-20: 1.25rem; // 20px\n  --size-24: 1.5rem; // 24px\n  --size-32: 2rem; // 32px\n  --size-40: 2.5rem; // 40px\n\n  // Button System (eg-button)\n  --button-padding: 0.375rem 0.75rem; // 6px 12px\n  --button-padding-lg: 0.5rem 1rem; // 8px 16px\n  --button-font-size: 0.75rem; // 12px\n  --button-border-radius: 0.25rem; // 4px\n  --button-transition: all 0.2s ease;\n\n  // Button Colors - Primary\n  --button-primary-bg: #2196f3;\n  --button-primary-color: #fff;\n  --button-primary-border: #2196f3;\n  --button-primary-hover-bg: #1976d2;\n  --button-primary-hover-border: #1565c0;\n\n  // Button Colors - Secondary\n  --button-secondary-bg: #fff;\n  --button-secondary-color: #666;\n  --button-secondary-border: #ddd;\n  --button-secondary-hover-bg: #f5f5f5;\n  --button-secondary-hover-border: #bbb;\n\n  // Button Colors - Ghost\n  --button-ghost-bg: transparent;\n  --button-ghost-color: #666;\n  --button-ghost-border: transparent;\n  --button-ghost-hover-bg: #f5f5f5;\n  --button-ghost-hover-border: #ddd;\n\n  // Button Colors - Toggle (Active state)\n  --button-toggle-active-bg: #fff3e0;\n  --button-toggle-active-color: #f57c00;\n  --button-toggle-active-border: #ff9800;\n\n  // Button Colors - Success (Save action)\n  --button-success-bg: #28a745;\n  --button-success-color: #fff;\n  --button-success-border: #28a745;\n  --button-success-hover-bg: #218838;\n  --button-success-hover-border: #1e7e34;\n\n  // Button Colors - Danger (Discard action)\n  --button-danger-bg: #dc3545;\n  --button-danger-color: #fff;\n  --button-danger-border: #dc3545;\n  --button-danger-hover-bg: #c82333;\n  --button-danger-hover-border: #bd2130;\n\n  // Epic Version Wrapper Height (sticky positioning)\n  --epic-version-wrapper-height: calc(\n    100% - 3rem\n  ); // :  - \n\n  @media (max-width: 900px) {\n    --epic-version-wrapper-height: calc(\n      100% - 6rem\n    ); // :  - 2\n  }\n}\n\n// Text overflow behavior: \n// : #kanban-root  .text-ellipsis-mode \n#kanban-root.text-ellipsis-mode {\n  --title-white-space: nowrap;\n  --title-text-overflow: ellipsis;\n  --title-word-break: normal;\n}\n\n// ========================================\n//  #kanban-root \n// ========================================\n#kanban-root {\n  * {\n    box-sizing: border-box;\n    margin: 0;\n    padding: 0;\n  }\n\n  font-family: -apple-system, BlinkMacSystemFont, \"Segoe UI\", Roboto, sans-serif;\n  height: 80vh;\n  background: #f5f5f5;\n\n  // CSS\n  &.vertical-mode {\n    --epic-width: 3rem;\n    --feature-width: 3rem;\n\n    .epic-cell {\n      writing-mode: vertical-rl;\n      text-orientation: mixed;\n\n      .add-feature-btn {\n        writing-mode: horizontal-tb;\n        font-size: 0.625rem;\n      }\n    }\n\n    .feature-cell {\n      writing-mode: vertical-rl;\n      text-orientation: mixed;\n      justify-content: center;\n    }\n  }\n\n  // \n  .app-container {\n    width: 100%;\n    height: 100%;\n    overflow: hidden;\n  }\n\n  //  + \n  .kanban-header {\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    flex-wrap: wrap; // A: \n    // padding: var(--space-lg);\n    padding-bottom: 0;\n    gap: var(--space-lg);\n    height: fit-content;\n    padding: 0.5rem;\n    position: sticky;\n    top: 0;\n    z-index: 300;\n    background: white;\n\n    // \n    &__container {\n      display: flex;\n      gap: var(--space-sm);\n      align-items: center;\n      width: 100%;\n      justify-content: space-between;\n      min-width: 0; // flex\n    }\n\n    // \n    &__controls {\n      display: flex;\n      gap: var(--space-sm);\n      align-items: center;\n      flex: 1; // \n      min-width: 0; // flex\n      flex-wrap: wrap; // \n    }\n  }\n\n  // \n  .kanban-fullscreen {\n    width: 100%;\n    height: 100%;\n    overflow: auto;\n  }\n\n  h1 {\n    padding-bottom: var(--space-lg);\n    color: #333;\n    margin: 0;\n  }\n\n  .test-info {\n    background: #fff3cd;\n    border: var(--border-width-thin) solid #ffc107;\n    padding: var(--space-xl);\n    padding-bottom: var(--space-lg); // padding\n    border-radius: var(--border-radius-lg);\n  }\n\n  // ========================================\n  // Status Indicators\n  // ========================================\n  .status-indicator {\n    display: inline-block;\n    width: 0.5rem;\n    height: 0.5rem;\n    border-radius: 50%;\n    margin-right: 0.25rem; // inlinegapmargin\n\n    &.status-open {\n      background: #4caf50;\n    }\n\n    &.status-closed {\n      background: #9e9e9e;\n    }\n  }\n\n  // ========================================\n  // Level 1: Epic  Version Grid\n  // ========================================\n  .epic-version-wrapper {\n    display: grid;\n    grid-template-rows: auto auto;\n    grid-template-columns: 1fr auto;\n    gap: 0;\n    overflow-x: scroll;\n    height: var(--epic-version-wrapper-height);\n\n    .epic-version-grid {\n      grid-row: 1;\n      grid-column: 1;\n    }\n\n    .new-version-wrapper {\n      grid-row: 1;\n      grid-column: 2;\n      position: sticky;\n      right: 0;\n      top: 0;\n      z-index: 200;\n      width: auto;\n      height: fit-content;\n    }\n\n    .new-epic-wrapper {\n      display: flex;\n      align-items: center;\n      justify-items: center;\n      z-index: 100;\n      height: 2rem;\n    }\n  }\n\n  // Epic  Feature  Version 3\n  .epic-feature-version-grid {\n    display: grid;\n    // grid-template-columnsJS\n    grid-template-rows: auto; // \n    grid-auto-rows: minmax(6.25rem, auto); // 2\n    gap: var(--space-md);\n    background: #e0e0e0;\n    padding: var(--space-md);\n  }\n\n  .scroll_information {\n    box-sizing: border-box;\n    grid-row: 2;\n    grid-column: 2;\n    position: sticky;\n    right: 0;\n    bottom: 0;\n    z-index: 300;\n    width: auto;\n    height: fit-content;\n\n    p {\n      width: auto;\n      height: auto;\n    }\n  }\n\n  // \n  .header-label {\n    background: #fff3e0;\n    border: var(--border-width-thin) solid #ff9800;\n    padding: var(--space-xs);\n    height: fit-content;\n    font-weight: bold;\n    font-size: 0.75rem;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    color: #e65100;\n    position: sticky;\n    top: 0;\n    z-index: 110;\n  }\n\n  // Epicspan\n  .epic-cell {\n    background: #dae8fc;\n    border: var(--border-width-normal) solid #6c8ebf;\n    border-left: var(--border-width-thick) solid #1976d2;\n    padding: var(--space-sm);\n    font-weight: bold;\n    font-size: 0.8125rem;\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n    align-items: stretch;\n    text-align: center;\n    position: sticky;\n    left: 0;\n    z-index: 100;\n\n    .epic-name {\n      flex: 1;\n      display: flex;\n      align-items: center;\n      justify-content: center;\n    }\n\n    .add-feature-btn {\n      margin-top: auto; // flexboxmargin-top:auto\n      width: 100%;\n    }\n\n    // ListTab\n    &--focused {\n      position: sticky; // \n      z-index: 900;\n    }\n\n    // \n    &--highlight {\n      animation: epic-feature-highlight 3s ease-in-out;\n    }\n  }\n\n  // Feature\n  .feature-cell {\n    background: #fff9c4;\n    border: var(--border-width-normal) solid #fbc02d;\n    padding: var(--space-sm);\n    font-weight: 600;\n    font-size: 0.8rem;\n    display: flex;\n    align-items: center;\n    min-height: 3.125rem;\n    position: sticky;\n    left: var(--epic-width);\n    z-index: 99;\n\n    // ListTab\n    &--focused {\n      position: sticky; // \n      z-index: 900;\n    }\n\n    // \n    &--highlight {\n      animation: epic-feature-highlight 3s ease-in-out;\n    }\n\n    // D&DFeature\n    &.draggable {\n      cursor: grab;\n      transition: all 0.2s ease;\n\n      &:hover {\n        background: #fff59d;\n        border-color: #f9a825;\n        box-shadow: 0 var(--size-2) var(--size-4) rgba(0, 0, 0, 0.1);\n      }\n\n      &:active {\n        cursor: grabbing;\n        opacity: 0.6;\n      }\n\n      // \n      &[data-dragging=\"true\"] {\n        opacity: 0.4;\n        border-style: dashed;\n      }\n\n      // \n      &[data-drop-target=\"true\"] {\n        background: #f0f4c3;\n        border: var(--border-width-normal) dashed #f57f17;\n        box-shadow: 0 0 var(--size-8) rgba(245, 127, 23, 0.4);\n      }\n    }\n  }\n\n  // UserStory (Version)\n  .us-cell {\n    background: #fafafa;\n    border: var(--border-width-normal) solid #bdbdbd;\n    padding: var(--space-md);\n    min-height: 6.25rem;\n\n    // Add Feature\n    &.empty-cell {\n      background: transparent;\n      border: var(--border-width-thin) dashed #e0e0e0;\n      min-height: 3rem;\n    }\n  }\n\n  // Version \n  .version-header {\n    display: flex;\n    flex-direction: row;\n    justify-content: center;\n    gap: 1rem;\n    background: #e1d5e7;\n    border: var(--border-width-normal) solid #9673a6;\n    padding: var(--space-sm);\n    font-weight: bold;\n    font-size: 0.8125rem;\n    text-align: center;\n    position: sticky;\n    top: 0;\n    z-index: 110;\n    height: fit-content;\n\n    .version-name {\n      margin-bottom: 0.25rem; // paddingmargin\n    }\n\n    .version-date {\n      font-size: 0.75rem;\n      font-weight: normal;\n      color: #666;\n    }\n\n    // \n    &.clickable {\n      cursor: pointer;\n      transition: background-color 0.2s ease;\n\n      &:hover {\n        background: #d4c5db;\n      }\n\n      &:active {\n        background: #c7b5ce;\n      }\n    }\n  }\n\n  // ========================================\n  // Level 3: User Story Grid\n  // ========================================\n  .user-story-grid {\n    display: grid;\n    grid-template-columns: 1fr;\n    gap: var(--user-story-gap);\n  }\n\n  .user-story {\n    min-width: 0; // Grid\n    background: #f9f9f9;\n    border: var(--border-width-thin) solid #e0e0e0;\n    border-radius: var(--border-radius-md);\n    padding: var(--user-story-padding);\n    cursor: move;\n    transition: all 150ms ease-in-out;\n    user-select: none; // \n\n    &:hover {\n      background: #f0f0f0;\n    }\n\n    &.dragging {\n      opacity: 0.5;\n    }\n\n    &.over {\n      transform: scale(1.03);\n      border-color: #2196f3;\n    }\n\n    &.closed {\n      opacity: 0.6;\n      filter: grayscale(0.5);\n\n      .user-story-header {\n        color: #9e9e9e;\n      }\n    }\n\n    &.overdue {\n      background: #ffebee;\n      border: var(--border-width-normal) solid #f44336;\n      border-left: var(--border-width-thick) solid #d32f2f;\n\n      &:hover {\n        background: #ffcdd2;\n      }\n    }\n\n    &.unassigned {\n      background: #fff3e0;\n      border: var(--border-width-normal) solid #ff9800;\n      border-left: var(--border-width-thick) solid #f57c00;\n\n      &:hover {\n        background: #ffe0b2;\n      }\n    }\n\n    // \n    &.overdue.unassigned {\n      background: #ffebee;\n      border: var(--border-width-normal) solid #f44336;\n      border-left: var(--border-width-thick) solid #d32f2f;\n\n      &:hover {\n        background: #ffcdd2;\n      }\n    }\n\n    .user-story-header {\n      display: flex;\n      align-items: center;\n      flex-direction: column;\n      gap: 0.2rem;\n      font-size: var(--font-user-story-header);\n      font-weight: bold;\n      padding-bottom: 0.45rem;\n      cursor: pointer;\n\n      &:hover {\n        background: #e8e8e8;\n      }\n\n      .main-info-wrapper {\n        display: flex;\n        align-items: center;\n        gap: 0.25rem;\n        flex: 1;\n        overflow: hidden;\n        width: 100%;\n      }\n\n      .title-wrapper {\n        flex: 1;\n        overflow: hidden;\n        text-overflow: var(--title-text-overflow);\n        white-space: var(--title-white-space);\n        word-break: var(--title-word-break);\n      }\n\n      .essential-info-wrapper {\n        display: flex;\n        align-items: center;\n        gap: 0.5rem;\n        flex-shrink: 0;\n        font-size: small;\n        width: 100%;\n      }\n\n      .issue-id-wrapper {\n        font-size: 0.7rem;\n        font-weight: normal;\n        padding-inline-start: 0.25rem;\n        flex-shrink: 0;\n      }\n\n      .assigned_to-name-wrapper,\n      .date-range-wrapper {\n        font-size: 0.75rem;\n        color: #666;\n        white-space: nowrap;\n      }\n    }\n  }\n\n  // ========================================\n  // Level 4: Task/Test/Bug Grid\n  // ========================================\n  .task-test-bug-grid {\n    display: grid;\n    grid-template-columns: 1fr;\n    gap: var(--task-container-gap);\n  }\n\n  // Container styles (Task, Test, Bug)\n  .task-container,\n  .test-container,\n  .bug-container {\n    border-radius: var(--border-radius-md);\n    padding: var(--task-container-padding);\n  }\n\n  .task-container {\n    background: #e3f2fd;\n    border: var(--border-width-thin) solid #1976d2;\n\n    .task-container-header {\n      background: #bbdefb;\n      color: #0d47a1;\n    }\n  }\n\n  .test-container {\n    background: #e8f5e9;\n    border: var(--border-width-thin) solid #388e3c;\n\n    .test-container-header {\n      background: #c8e6c9;\n      color: #1b5e20;\n    }\n  }\n\n  .bug-container {\n    background: #ffebee;\n    border: var(--border-width-thin) solid #c62828;\n\n    .bug-container-header {\n      background: #ffcdd2;\n      color: #b71c1c;\n    }\n  }\n\n  // Container headers\n  .task-container-header,\n  .test-container-header,\n  .bug-container-header {\n    font-size: var(--font-task-header);\n    font-weight: bold;\n    padding-bottom: var(--task-item-gap);\n    padding: var(--task-item-gap) var(--task-container-padding);\n    border-radius: var(--border-radius-sm);\n  }\n\n  // Item grids\n  .task-item-grid,\n  .test-item-grid,\n  .bug-item-grid {\n    display: grid;\n    grid-template-columns: 1fr;\n    gap: var(--task-item-gap);\n  }\n\n  // Item styles\n  .task-item,\n  .test-item,\n  .bug-item {\n    min-width: 0; // Grid\n    display: flex;\n    align-items: center;\n    flex-direction: column;\n    gap: 0.2rem;\n    background: #fff;\n    padding: var(--task-item-padding-v) var(--task-item-padding-h);\n    font-size: var(--font-task-it