: #ffe0b2;\n      }\n    }\n  }\n\n  .bug-item {\n    border: var(--border-width-thin) solid #ef5350;\n\n    &:hover {\n      background: #ffcdd2;\n    }\n\n    &.over {\n      border-color: #c62828;\n      background: #ef9a9a;\n    }\n\n    &.unassigned {\n      background: #fff3e0;\n      border: var(--border-width-normal) solid #ff9800;\n      border-left: var(--border-width-thick) solid #f57c00;\n\n      &:hover {\n        background: #ffe0b2;\n      }\n    }\n  }\n\n  // ========================================\n  // Add Buttons (All Levels)\n  // ========================================\n  .add-button {\n    background: #fafafa;\n    color: #666;\n    padding: var(--space-sm);\n    text-align: center;\n    cursor: pointer;\n    border-radius: var(--border-radius-lg);\n    font-size: 0.6875rem;\n    font-weight: bold;\n    transition: all 150ms ease-in-out;\n    order: 9999;\n\n    &:active {\n      transform: scale(0.98);\n    }\n  }\n\n  // Add Epic/Version buttons\n  .add-epic-btn,\n  .add-version-btn {\n    padding: var(--space-md);\n    text-align: center;\n    cursor: pointer;\n    border-radius: var(--border-radius-lg);\n    font-size: 0.75rem;\n    font-weight: bold;\n    transition: all 150ms ease-in-out;\n    width: 100%;\n    height: 100%;\n  }\n\n  .add-epic-btn {\n    background: #e3f2fd;\n    border: var(--border-width-normal) dashed #1976d2;\n    color: #1565c0;\n\n    &:hover {\n      background: #bbdefb;\n      border-color: #1565c0;\n    }\n  }\n\n  .add-version-btn {\n    background: #f3e5f5;\n    border: var(--border-width-normal) dashed #7b1fa2;\n    color: #6a1b9a;\n  }\n\n  // Add Feature button\n  .add-feature-btn {\n    border: var(--border-width-normal) dashed #f57f17;\n    color: #f57f17;\n    min-height: 3.75rem;\n    font-size: 0.75rem;\n\n    &:hover {\n      background: #fff9c4;\n      border-color: #f57f17;\n    }\n  }\n\n  // Add User Story button\n  .add-user-story-btn {\n    min-height: 2.5rem;\n    font-size: 0.625rem;\n    p