  }\n\n    &__left {\n      flex-shrink: 0;\n    }\n\n    &__right {\n      flex-shrink: 0;\n      flex-grow: 1;\n    }\n\n    &__splitter {\n      position: relative;\n      width: var(--size-8);\n      flex-shrink: 0;\n      cursor: col-resize;\n      background: #e0e0e0;\n      transition: background 0.2s;\n\n      &:hover,\n      &:focus {\n        background: #bbb;\n        outline: none;\n      }\n\n      &:active {\n        background: #999;\n      }\n    }\n\n    &__splitter-handle {\n      position: absolute;\n      top: 50%;\n      left: 50%;\n      transform: translate(-50%, -50%);\n      width: var(--size-4);\n      height: var(--size-40);\n      background: #fff;\n      border-radius: var(--border-radius-sm);\n      pointer-events: none;\n    }\n  }\n\n  // ========================================\n  // Issue Detail Pane ()\n  // ========================================\n  .issue-detail-pane {\n    display: flex;\n    flex-direction: column;\n    height: 100%;\n    background: #fff;\n    border-left: var(--border-width-thin) solid #e0e0e0;\n\n    &--empty {\n      display: flex;\n      align-items: center;\n      justify-content: center;\n    }\n\n    &__placeholder {\n      text-align: center;\n      color: #999;\n    }\n\n    &__placeholder-icon {\n      margin-bottom: var(\n        --size-16\n      ); // paddingmargin\n    }\n\n    &__placeholder-text {\n      font-size: var(--size-14);\n      margin: 0;\n    }\n\n    &__header {\n      display: flex;\n      align-items: center;\n      justify-content: space-between;\n      padding: var(--size-12) var(--size-16);\n      border-bottom: var(--border-width-thin) solid #e0e0e0;\n      background: #f5f5f5;\n      flex-shrink: 0;\n    }\n\n    &__title {\n      margin: 0;\n      font-size: var(--size-16);\n      font-weight: 600;\n      color: #333;\n    }\n\n    &__actions {\n      display: flex;\n      align-items: center;\n      gap: var(--size-12);\n    }\n\n    &__status {\n      font-size: var(--size-12);\n      color: #666;\n    }\n\n    &__reload {\n      display: flex;\n      align-items: center;\n      justify-content: center;\n      width: var(--size-32);\n      height: var(--size-32);\n      padding: 0;\n      border: var(--border-width-thin) solid #ddd;\n      border-radius: var(--button-border-radius);\n      background: #fff;\n      color: #666;\n      cursor: pointer;\n      transition: all 0.2s;\n\n      &:hover {\n        background: #f0f0f0;\n        border-color: #bbb;\n        color: #333;\n      }\n\n      &:active {\n        transform: scale(0.95);\n      }\n    }\n\n    &__content {\n      position: relative;\n      flex: 1;\n      overflow: hidden;\n    }\n\n    &__loading,\n    &__error {\n      position: absolute;\n      top: 50%;\n      left: 50%;\n      transform: translate(-50%, -50%);\n      text-align: center;\n    }\n\n    &__spinner {\n      width: var(--size-40);\n      height: var(--size-40);\n      margin: 0 auto var(--size-16); // margin:auto\n      border: var(--border-width-medium) solid #f3f3f3;\n      border-top: var(--border-width-medium) solid #3498db;\n      border-radius: 50%;\n      animation: spin 1s linear infinite;\n    }\n\n    &__error {\n      color: #d32f2f;\n\n      p {\n        margin-bottom: var(\n          --size-16\n        ); // paddingmargin\n      }\n\n      button {\n        padding: var(--size-8) var(--size-16);\n        border: var(--border-width-thin) solid #d32f2f;\n        border-radius: var(--button-border-radius);\n        background: #fff;\n        color: #d32f2f;\n        cursor: pointer;\n        transition: all 0.2s;\n\n        &:hover {\n          background: #d32f2f;\n          color: #fff;\n        }\n      }\n    }\n\n    &__iframe {\n      width: 100%;\n      height: 100%;\n      border: none;\n    }\n  }\n\n  // ========================================\n  // Button System (eg-button)\n  // ========================================\n  .eg-button {\n    display: flex;\n    align-items: center;\n    gap: 0.25rem;\n    padding: var(--button-padding);\n    border: var(--border-width-thin) solid var(--button-secondary-border);\n    border-radius: var(--button-border-radius);\n    background: var(--button-secondary-bg);\n    color: var(--button-secondary-color);\n    font-size: var(--button-font-size);\n    font-weight: 500;\n    cursor: pointer;\n    transition: var(--button-transition);\n    white-space: nowrap;\n\n    &:hover {\n      background: var(--button-secondary-hover-bg);\n      border-color: var(--button-secondary-hover-border);\n    }\n\n    &:active {\n      transform: scale(0.98);\n    }\n\n    &:disabled {\n      opacity: 0.6;\n      cursor: not-allowed;\n      pointer-events: none;\n    }\n\n    svg {\n      flex-shrink: 0;\n    }\n\n    // B: \n    span {\n      @media (max-width: 1200px) {\n        display: none;\n      }\n    }\n\n    // Primary variant\n    &--primary {\n      background: var(--button-primary-bg);\n      color: var(--button-primary-color);\n      border-color: var(--button-primary-border);\n\n      &:hover {\n        background: var(--button-primary-hover-bg);\n        border-color: var(--button-primary-hover-border);\n      }\n    }\n\n    // Secondary variant (default)\n    &--secondary {\n      // Already using default values\n    }\n\n    // Ghost variant\n    &--ghost {\n      background: var(--button-ghost-bg);\n      color: var(--button-ghost-color);\n      border-color: var(--button-ghost-border);\n\n      &:hover {\n        background: var(--button-ghost-hover-bg);\n        border-color: var(--button-ghost-hover-border);\n      }\n    }\n\n    // Success variant (Save action)\n    &--success {\n      background: var(--button-success-bg);\n      color: var(--button-success-color);\n      border-color: var(--button-success-border);\n      font-weight: bold;\n\n      &:hover:not(:disabled) {\n        background: var(--button-success-hover-bg);\n        border-color: var(--button-success-hover-border);\n      }\n\n      &:disabled {\n        cursor: wait;\n      }\n    }\n\n    // Danger variant (Discard action)\n    &--danger {\n      background: var(--button-danger-bg);\n      color: var(--button-danger-color);\n      border-color: var(--button-danger-border);\n\n      &:hover:not(:disabled) {\n        background: var(--button-danger-hover-bg);\n        border-color: var(--button-danger-hover-border);\n      }\n\n      &:disabled {\n        cursor: not-allowed;\n      }\n    }\n\n    // Toggle variant\n    &--toggle {\n      &.eg-button--active {\n        background: var(--button-toggle-active-bg);\n        color: var(--button-toggle-active-color);\n        border-color: var(--button-toggle-active-border);\n\n        &:hover {\n          background: #ffe0b2;\n        }\n      }\n    }\n\n    // Icon variant (for buttons with SVG icons)\n    &--icon {\n      &.expand:hover {\n        background: #e3f2fd;\n        border-color: #2196f3;\n        color: #1976d2;\n      }\n\n      &.collapse:hover {\n        background: #fafafa;\n        border-color: #999;\n        color: #555;\n      }\n    }\n\n    // Large variant\n    &--lg {\n      padding: var(--button-padding-lg);\n    }\n  }\n\n  // ========================================\n  // Detail Pane Toggle ()\n  // ========================================\n  .detail-pane-toggle {\n    display: flex;\n    align-items: center;\n    gap: 0.5rem;\n    padding: 0.5rem 1rem;\n    border: var(--border-width-thin) solid #ddd;\n    border-radius: var(--button-border-radius);\n    background: #fff;\n    color: #666;\n    font-size: 0.875rem;\n    font-weight: 500;\n    cursor: pointer;\n    transition: all 0.2s ease;\n    white-space: nowrap;\n\n    &:hover {\n      background: #f5f5f5;\n      border-color: #bbb;\n      color: #333;\n    }\n\n    &:active {\n      transform: scale(0.98);\n    }\n\n    &.active {\n      background: #e3f2fd;\n      border-color: #2196f3;\n      color: #1976d2;\n\n      &:hover {\n        background: #bbdefb;\n      }\n    }\n\n    svg {\n      flex-shrink: 0;\n    }\n\n    &__label {\n      // \n      @media (max-width: 768px) {\n        display: none;\n      }\n    }\n  }\n\n  // ========================================\n  // UserStory Toggle Group ()\n  // ========================================\n  .userstory-toggle-group {\n    display: flex;\n    gap: 0.25rem;\n\n    .userstory-toggle-button {\n      display: flex;\n      align-items: center;\n      gap: 0.25rem;\n      padding: 0.375rem 0.75rem;\n      border: var(--border-width-thin) solid #ddd;\n      border-radius: var(--button-border-radius);\n      background: #fff;\n      color: #666;\n      font-size: 0.75rem;\n      font-weight: 500;\n      cursor: pointer;\n      transition: all 0.2s ease;\n      white-space: nowrap;\n\n      &:hover {\n        background: #f5f5f5;\n        border-color: #bbb;\n        color: #333;\n      }\n\n      &:active {\n        transform: scale(0.98);\n      }\n\n      &.expand:hover {\n        background: #e3f2fd;\n        border-color: #2196f3;\n        color: #1976d2;\n      }\n\n      &.collapse:hover {\n        background: #fafafa;\n        border-color: #999;\n        color: #555;\n      }\n\n      svg {\n        flex-shrink: 0;\n      }\n\n      span {\n        // \n        @media (max-width: 768px) {\n          display: none;\n        }\n      }\n    }\n  }\n\n  // ========================================\n  // Sort Controls (UI)\n  // ========================================\n  .sort-controls {\n    display: flex;\n    gap: 1rem;\n    padding: var(--space-md);\n    background: #fff;\n    border: var(--border-width-thin) solid #ddd;\n    border-radius: var(--border-radius-lg);\n    margin-bottom: var(\n      --space-md\n    ); // paddingmargin\n    flex-wrap: wrap;\n    align-items: center;\n  }\n\n  .sort-selector {\n    display: flex;\n    align-items: center;\n    gap: 0.5rem;\n\n    &__label {\n      font-size: 0.8125rem; // 13px\n      font-weight: bold;\n      color: #333;\n      white-space: nowrap;\n    }\n\n    &__controls {\n      display: flex;\n      gap: 0.375rem; // 6px\n    }\n\n    &__field,\n    &__direction {\n      padding: 0.375rem 0.5rem; // 6px 8px\n      font-size: 0.8125rem; // 13px\n      border: var(--border-width-thin) solid #ccc;\n      border-radius: var(--border-radius-md);\n      background-color: #fff;\n      font-family: inherit;\n      cursor: pointer;\n      transition: all 0.2s;\n\n      &:hover {\n        border-color: #999;\n      }\n\n      &:focus {\n        outline: none;\n        border-color: #628db6;\n        box-shadow: 0 0 var(--size-3) rgba(98, 141, 182, 0.3);\n      }\n    }\n\n    &__field {\n      min-width: 5rem; // 80px\n    }\n\n    &__direction {\n      min-width: 4rem; // 64px\n    }\n  }\n} // #kanban-root\n\n// ========================================\n// Modal Forms (Version & Issue Creation)\n// Headless UI  body #kanban-root \n// ========================================\n.version-form-modal,\n.issue-form-modal {\n  position: relative;\n  z-index: 9998;\n\n  .modal-overlay {\n    position: fixed;\n    inset: 0;\n    background-color: rgba(0, 0, 0, 0.5);\n    z-index: 9998;\n  }\n\n  .modal-container {\n    position: fixed;\n    inset: 0;\n    z-index: 9999;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    padding: 1rem;\n  }\n\n  .modal-panel {\n    background-color: #fff;\n    border: var(--border-width-thin) solid #ccc;\n    border-radius: var(--button-border-radius);\n    box-shadow: 0 var(--size-4) var(--size-6) rgba(0, 0, 0, 0.1);\n    max-width: 31.25rem; // 500px\n    width: 100%;\n    max-height: 90vh;\n    overflow-y: auto;\n    padding: 1.25rem; // 20px\n  }\n\n  .modal-title {\n    font-size: 1rem;\n    font-weight: bold;\n    color: #333;\n    border-bottom: var(--border-width-thin) solid #e0e0e0;\n    padding-bottom: 0.625rem; // 10px\n    margin-bottom: 1.25rem; // 20px\n  }\n\n  .modal-form {\n    display: flex;\n    flex-direction: column;\n    gap: 1rem;\n  }\n\n  .form-group {\n    display: flex;\n    flex-direction: column;\n    gap: 0.375rem; // 6px\n  }\n\n  .form-label {\n    font-size: 0.8125rem; // 13px\n    font-weight: bold;\n    color: #333;\n\n    .required {\n      color: #c00;\n      margin-left: 0.125rem; // 2px\n    }\n  }\n\n  .form-input,\n  .form-textarea {\n    padding: 0.375rem 0.5rem; // 6px 8px\n    font-size: 0.8125rem; // 13px\n    border: var(--border-width-thin) solid #ccc;\n    border-radius: var(--border-radius-md);\n    background-color: #fff;\n    font-family: inherit;\n\n    &:focus {\n      outline: none;\n      border-color: #628db6;\n      box-shadow: 0 0 var(--size-3) rgba(98, 141, 182, 0.3);\n    }\n\n    &:disabled {\n      background-color: #f5f5f5;\n      cursor: not-allowed;\n      opacity: 0.6;\n    }\n\n    &::placeholder {\n      color: #999;\n    }\n  }\n\n  .form-textarea {\n    resize: vertical;\n    min-height: 6.25rem; // 100px\n  }\n\n  .modal-actions {\n    display: flex;\n    justify-content: flex-end;\n    gap: 0.5rem;\n    padding-top: 1rem;\n    border-top: var(--border-width-thin) solid #e0e0e0;\n  }\n\n  .btn-cancel,\n  .btn-submit {\n    padding: 0.375rem 0.75rem; // 6px 12px\n    font-size: 0.8125rem; // 13px\n    border-radius: var(--border-radius-md);\n    border: var(--border-width-thin) solid;\n    cursor: pointer;\n    font-family: inherit;\n    transition: background-color 0.2s, border-color 0.2s;\n\n    &:disabled {\n      opacity: 0.6;\n      cursor: not-allowed;\n    }\n  }\n\n  .btn-cancel {\n    background-color: #fff;\n    border-color: #ccc;\n    color: #333;\n\n    &:hover:not(:disabled) {\n      background-color: #f5f5f5;\n      border-color: #999;\n    }\n  }\n\n  .btn-submit {\n    background-color: #628db6;\n    border-color: #628db6;\n    color: #fff;\n\n    &:hover:not(:disabled) {\n      background-color: #507a9d;\n      border-color: #507a9d;\n    }\n  }\n}\n\n// ========================================\n// Feature Header\n// ========================================\n.feature-header {\n  display: flex;\n  align-items: center;\n  gap: 0.375rem;\n  cursor: pointer;\n  transition: background 0.2s ease;\n\n  &:hover {\n    background: rgba(0, 0, 0, 0.05);\n  }\n}\n\n// ========================================\n// User Story Collapse Toggle Button\n// ========================================\n.user-story-collapse-toggle {\n  background: none;\n  border: none;\n  cursor: pointer;\n  padding: 0.25rem;\n  font-size: 0.75rem;\n  color: #666;\n  transition: all 0.2s ease;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  flex-shrink: 0;\n\n  &:hover {\n    color: #333;\n    background: rgba(0, 0, 0, 0.05);\n    border-radius: 0.125rem;\n  }\n\n  &:active {\n    color: #000;\n  }\n}\n\n// ========================================\n// Keyframe Animations\n// ========================================\n@keyframes spin {\n  0% {\n    transform: rotate(0deg);\n  }\n  100% {\n    transform: rotate(360deg);\n  }\n}\n\n// ========================================\n// Filter Panel Styles\n// ========================================\n.filter-panel {\n  position: relative;\n  display: inline-block;\n\n  .filter-toggle-btn {\n    padding: 0.5rem 1rem;\n    background-color: #007bff;\n    color: white;\n    border: none;\n    border-radius: var(--border-radius-md);\n    cursor: pointer;\n    font-size: 0.875rem;\n    font-weight: 500;\n    transition: background-color 0.2s ease;\n\n    &:hover {\n      background-color: #0056b3;\n    }\n\n    &:active {\n      background-color: #004085;\n    }\n  }\n\n  .filter-dropdown {\n    position: absolute;\n    top: 100%;\n    left: 0;\n    margin-top: 0.5rem; // paddingmargin\n    background-color: white;\n    border: var(--border-width-thin) solid #ddd;\n    border-radius: var(--border-radius-md);\n    box-shadow: 0 0.25rem 0.5rem rgba(0, 0, 0, 0.1);\n    padding: 1rem;\n    min-width: 18.75rem;\n    max-width: 25rem;\n    max-height: 31.25rem;\n    overflow-y: auto;\n    z-index: 1000;\n\n    .filter-section {\n      padding-bottom: 1rem;\n      border-bottom: var(--border-width-thin) solid #eee;\n\n      &:not(:first-child) {\n        padding-top: 1rem;\n      }\n\n      &:last-of-type {\n        border-bottom: none;\n        padding-bottom: 0.5rem;\n      }\n\n      h4 {\n        font-size: 0.875rem;\n        font-weight: 600;\n        color: #333;\n        margin: 0 0 0.5rem 0;\n        padding: 0;\n      }\n\n      .filter-options {\n        display: flex;\n        flex-direction: column;\n        gap: 0.5rem;\n        max-height: 12.5rem;\n        overflow-y: auto;\n        padding: 0.25rem;\n\n        .filter-checkbox {\n          display: flex;\n          align-items: center;\n          gap: 0.5rem;\n          cursor: pointer;\n          padding: 0.25rem;\n          border-radius: var(--border-radius-sm);\n          transition: background-color 0.2s ease;\n\n          &:hover {\n            background-color: #f5f5f5;\n          }\n\n          input[type=\"checkbox\"] {\n            cursor: pointer;\n            width: 1rem;\n            height: 1rem;\n            margin: 0;\n          }\n\n          span {\n            font-size: 0.875rem;\n            color: #555;\n            user-select: none;\n          }\n        }\n\n        .no-options {\n          font-size: 0.875rem;\n          color: #999;\n          font-style: italic;\n          margin: 0;\n          padding: 0.5rem;\n          text-align: center;\n        }\n      }\n    }\n\n    .filter-actions {\n      display: flex;\n      gap: 0.5rem;\n      padding-top: 1rem;\n      border-top: var(--border-width-thin) solid #eee;\n\n      button {\n        flex: 1;\n        padding: 0.5rem 1rem;\n        border: none;\n        border-radius: var(--border-radius-sm);\n        cursor: pointer;\n        font-size: 0.875rem;\n        font-weight: 500;\n        transition: background-color 0.2s ease;\n      }\n\n      .filter-apply-btn {\n        background-color: #28a745;\n        color: white;\n\n        &:hover {\n          background-color: #218838;\n        }\n\n        &:active {\n          background-color: #1e7e34;\n        }\n      }\n\n      .filter-clear-btn {\n        background-color: #6c757d;\n        color: white;\n\n        &:hover {\n          background-color: #5a6268;\n        }\n\n        &:active {\n          background-color: #545b62;\n        }\n      }\n    }\n  }\n}\n\n// ========================================\n// Settings Dropdown (Custom Implementation)\n// ========================================\n\n// Dropdown Container\n.dropdown {\n  position: relative;\n  display: inline-block;\n}\n\n.dropdown-trigger {\n  cursor: pointer;\n\n  button {\n    padding: 0.5rem 1rem;\n    font-size: 0.875rem;\n    font-weight: 600;\n    color: #333;\n    background-color: #fff;\n    border: var(--border-width-thin) solid #ccc;\n    border-radius: var(--border-radius-md);\n    cursor: pointer;\n    transition: all 0.2s;\n    display: flex;\n    align-items: center;\n    gap: 0.25rem;\n\n    &:hover {\n      background-color: #f8f9fa;\n      border-color: #999;\n    }\n\n    &:focus {\n      outline: none;\n      border-color: #628db6;\n      box-shadow: 0 0 0 var(--size-3) rgba(98, 141, 182, 0.1);\n    }\n\n    &:active {\n      background-color: #e9ecef;\n    }\n  }\n}\n\n.dropdown-content {\n  position: absolute;\n  top: calc(100% + var(--size-4));\n  min-width: 20rem;\n  max-width: 25rem;\n  background-color: #fff;\n  border: var(--border-width-thin) solid #ddd;\n  border-radius: var(--border-radius-lg);\n  box-shadow: 0 var(--size-4) var(--size-12) rgba(0, 0, 0, 0.15);\n  padding: 0.5rem;\n  z-index: 1000;\n  animation: slideDownAndFade 200ms cubic-bezier(0.16, 1, 0.3, 1);\n\n  &.dropdown-align-start {\n    left: 0;\n  }\n\n  &.dropdown-align-end {\n    right: 0;\n  }\n}\n\n.dropdown-label {\n  padding: 0.5rem 0.75rem;\n  font-size: 0.75rem;\n  font-weight: 700;\n  color: #6c757d;\n  text-transform: uppercase;\n  letter-spacing: 0.05em;\n}\n\n.dropdown-section {\n  padding: 0.25rem 0.5rem;\n  margin: 0.25rem 0;\n\n  // \n  .sort-selector {\n    display: flex;\n    flex-direction: column;\n    gap: 0.5rem;\n    padding: 0.5rem;\n    background-color: #f8f9fa;\n    border-radius: var(--border-radius-md);\n\n    &__label {\n      font-size: 0.8125rem;\n      font-weight: 600;\n      color: #495057;\n    }\n\n    &__controls {\n      display: flex;\n      gap: 0.5rem;\n    }\n\n    &__field,\n    &__direction {\n      flex: 1;\n      padding: 0.375rem 0.5rem;\n      font-size: 0.8125rem;\n      border: var(--border-width-thin) solid #ced4da;\n      border-radius: var(--border-radius-md);\n      background-color: #fff;\n\n      &:hover {\n        border-color: #999;\n      }\n\n      &:focus {\n        outline: none;\n        border-color: #628db6;\n        box-shadow: 0 0 0 var(--size-2) rgba(98, 141, 182, 0.1);\n      }\n    }\n  }\n\n  // \n  button {\n    width: 100%;\n    text-align: left;\n    padding: 0.5rem 0.75rem;\n    font-size: 0.875rem;\n    background-color: transparent;\n    border: none;\n    border-radius: var(--border-radius-md);\n    cursor: pointer;\n    transition: background-color 0.2s;\n\n    &:hover {\n      background-color: #f8f9fa;\n    }\n\n    &:active {\n      background-color: #e9ecef;\n    }\n  }\n}\n\n.dropdown-separator {\n  height: var(--border-width-thin);\n  background-color: #dee2e6;\n  margin: 0.5rem 0;\n}\n\n// \n@keyframes slideDownAndFade {\n  from {\n    opacity: 0;\n    transform: translateY(calc(-1 * var(--size-8)));\n  }\n  to {\n    opacity: 1;\n    transform: translateY(0);\n  }\n}\n\n// ========================================\n// Triple Split Layout\n// ========================================\n#kanban-root {\n  .triple-split-layout {\n    display: flex;\n    width: 100%;\n    height: 100%;\n    overflow: hidden;\n\n    &--dragging {\n      user-select: none;\n      cursor: col-resize;\n    }\n\n 