/**
 * Phase 2
 *
 * @param issue - issue
 * @param query - 
 * @param target - subject/description/all
 * @returns true
 */
function matchesTarget(issue, query, target) {
    // TODO: Phase 2
    switch (target) {
        case 'subject':
            return issue.subject.toLowerCase().includes(query);
        case 'description':
            return (issue.description?.toLowerCase().includes(query) ?? false);
        case 'all':
            return issue.subject.toLowerCase().includes(query) ||
                (issue.description?.toLowerCase().includes(query) ?? false);
    }
}
