import React, { useState } from 'react';
import { useSortedEpicsAndFeatures } from '../../hooks/useSortedEpicsAndFeatures';
import { useStore } from '../../store/useStore';
import { highlightIssue, scrollToIssue, expandParentUserStory, enableFocusMode } from '../../utils/domUtils';
import { StatsToggle } from '../common/StatsToggle';
/**
 * ListTab 
 *
 * Epic/Feature
 * <details>/<summary>Epic
 *
 * - Epic: 
 * - (): 
 * - : Epic
 */
export const ListTab = () => {
    const entities = useStore(state => state.entities);
    const isStatsVisible = useStore(state => state.isStatsVisible);
    const [searchQuery, setSearchQuery] = useState('');
    // 
    const { sortedEpicOrder, getSortedFeatureIds } = useSortedEpicsAndFeatures();
    // EpicFeature
    const buildHierarchy = () => {
        return sortedEpicOrder.map(epicId => {
            const epic = entities.epics[epicId];
            if (!epic)
                return null;
            const featureIds = getSortedFeatureIds(epicId);
            const features = featureIds
                .map(featureId => entities.features[featureId])
                .filter(Boolean);
            return { epic, features };
        }).filter(Boolean);
    };
    // 
    const filterHierarchy = (hierarchy) => {
        if (!searchQuery.trim())
            return hierarchy;
        const query = searchQuery.toLowerCase();
        return hierarchy
            .map(item => {
            if (!item)
                return null;
            const epicMatches = item.epic.subject.toLowerCase().includes(query);
            const filteredFeatures = item.features.filter(feature => feature.subject.toLowerCase().includes(query));
            // EpicFeature
            if (epicMatches || filteredFeatures.length > 0) {
                return {
                    epic: item.epic,
                    features: epicMatches ? item.features : filteredFeatures
                };
            }
            return null;
        })
            .filter(Boolean);
    };
    const hierarchy = filterHierarchy(buildHierarchy());
    const handleEpicClick = (epicId, e) => {
        // <summary>
        e.preventDefault();
        e.stopPropagation();
        console.log(' [ListTab] Epic title clicked:', epicId);
        // Epic
        expandParentUserStory(epicId, 'epic');
        // DOM
        const scrolled = scrollToIssue(epicId, 'epic');
        if (scrolled) {
            // 
            enableFocusMode(epicId, 'epic');
            // 
            highlightIssue(epicId, 'epic');
        }
        else {
            console.warn(` Epic DOM element not found: ${epicId}`);
        }
    };
    const handleFeatureClick = (featureId, e) => {
        e.stopPropagation(); // details
        console.log(' [ListTab] Feature clicked:', featureId);
        // Feature
        expandParentUserStory(featureId, 'feature');
        // DOM
        const scrolled = scrollToIssue(featureId, 'feature');
        if (scrolled) {
            // 
            enableFocusMode(featureId, 'feature');
            // 
            highlightIssue(featureId, 'feature');
        }
        else {
            console.warn(` Feature DOM element not found: ${featureId}`);
        }
    };
    return (React.createElement("div", { className: "list-tab" },
        React.createElement("div", { className: "list-tab__header" },
            React.createElement("h3", { className: "list-tab__title" }, "Epic / Feature \u4E00\u89A7"),
            React.createElement("p", { className: "list-tab__subtitle" },
                hierarchy.length,
                "\u500B\u306EEpic")),
        React.createElement("div", { className: "list-tab__control" },
            React.createElement("div", { className: "list-tab__search" },
                React.createElement("input", { type: "text", className: "list-tab__search-input", placeholder: "Epic / Feature \u3092\u691C\u7D22...", value: searchQuery, onChange: (e) => setSearchQuery(e.target.value) }),
                searchQuery && (React.createElement("button", { className: "list-tab__search-clear", onClick: () => setSearchQuery(''), title: "\u691C\u7D22\u3092\u30AF\u30EA\u30A2" }, "\u2715"))),
            React.createElement(StatsToggle, null)),
        React.createElement("div", { className: "list-tab__content" }, hierarchy.length === 0 ? (React.createElement("div", { className: "list-tab__empty" },
            React.createElement("p", null, "\uD83D\uDCED Epic\u304C\u3042\u308A\u307E\u305B\u3093"))) : (React.createElement("div", { className: "list-tab__tree" }, hierarchy.map((item) => {
            if (!item)
                return null;
            const { epic, features } = item;
            return (React.createElement("details", { key: epic.id, open: true, className: "list-tab__epic-details" },
                React.createElement("summary", { className: "list-tab__epic-summary" },
                    React.createElement("div", { className: "list-tab__epic-marker" }, "\uD83D\uDCE6"),
                    React.createElement("div", { className: "list-tab__epic-content", onClick: (e) => handleEpicClick(epic.id, e) },
                        React.createElement("div", { className: "list-tab__epic-subject" }, epic.subject),
                        isStatsVisible && (React.createElement("div", { className: "list-tab__epic-stats" },
                            epic.statistics.total_features,
                            "\u4EF6\u306EFeature",
                            ' ',
                            "\u30FB",
                            ' ',
                            Math.round(epic.statistics.completion_percentage),
                            "%\u5B8C\u4E86")))),
                features.length > 0 && (React.createElement("ul", { className: "list-tab__features" }, features.map((feature) => (React.createElement("li", { key: feature.id, className: "list-tab__feature-item", onClick: (e) => handleFeatureClick(feature.id, e) },
                    React.createElement("div", { className: "list-tab__feature-icon" }, "\u2728"),
                    React.createElement("div", { className: "list-tab__feature-content" },
                        React.createElement("div", { className: "list-tab__feature-subject" }, feature.subject),
                        isStatsVisible && (React.createElement("div", { className: "list-tab__feature-stats" },
                            feature.statistics.total_user_stories,
                            "\u4EF6\u306E\u30B9\u30C8\u30FC\u30EA\u30FC",
                            ' ',
                            "\u30FB",
                            ' ',
                            Math.round(feature.statistics.completion_percentage),
                            "%\u5B8C\u4E86"))))))))));
        }))))));
};
