import { useEffect, useRef } from 'react';
import { draggable, dropTargetForElements } from '@atlaskit/pragmatic-drag-and-drop/element/adapter';
import { combine } from '@atlaskit/pragmatic-drag-and-drop/combine';
/**
 * 
 * FeatureUserStoryTaskTestBug 
 */
export const useDraggableAndDropTarget = ({ type, id, data = {}, canDrop, onDragStart, onDragEnter, onDragLeave, onDrop, onDragEnd }) => {
    const ref = useRef(null);
    useEffect(() => {
        const element = ref.current;
        if (!element) {
            console.warn(' useDraggableAndDropTarget: element is null', { type, id });
            return;
        }
        console.log(' Setting up drag and drop for:', { type, id, element });
        const cleanup = combine(
        // Draggable
        draggable({
            element,
            getInitialData: () => ({
                type,
                id,
                instanceId: Symbol('nested-grid-test'), // ID
                ...data
            }),
            onDragStart: () => {
                console.log(' onDragStart:', { type, id });
                element.classList.add('dragging');
                onDragStart?.();
            },
            onDrop: () => {
                console.log(' onDrop (draggable):', { type, id });
                element.classList.remove('dragging');
                onDragEnd?.();
            }
        }), 
        // DropTarget
        dropTargetForElements({
            element,
            getData: () => ({
                type,
                id,
                ...data
            }),
            getIsSticky: () => true,
            canDrop: ({ source }) => {
                if (canDrop) {
                    return canDrop(source.data);
                }
                // : ID
                return source.data.type === type && source.data.id !== id;
            },
            onDragEnter: () => {
                console.log(' onDragEnter:', { type, id });
                element.classList.add('over');
                onDragEnter?.();
            },
            onDragLeave: () => {
                console.log(' onDragLeave:', { type, id });
                element.classList.remove('over');
                onDragLeave?.();
            },
            onDrop: ({ source }) => {
                console.log(' onDrop (dropTarget):', { source: source.data, target: { type, id } });
                element.classList.remove('over');
                onDrop?.(source.data);
            }
        }));
        return cleanup;
        // : idtype
        // 
        // ref
    }, [id, type]);
    return ref;
};
