import React, { useState } from 'react';
import { UserStory } from './UserStory';
import { AddButton } from '../common/AddButton';
import { IssueFormModal } from '../common/IssueFormModal';
import { useStore } from '../../store/useStore';
export const UserStoryGridForCell = ({ epicId, featureId, versionId, storyIds }) => {
    const createUserStory = useStore((state) => state.createUserStory);
    const users = useStore((state) => state.entities.users || {});
    const [isModalOpen, setIsModalOpen] = useState(false);
    const usersList = Object.values(users);
    const handleAddUserStory = () => {
        setIsModalOpen(true);
    };
    const handleSubmit = async (data) => {
        try {
            await createUserStory(featureId, {
                subject: data.subject,
                description: data.description,
                parent_feature_id: featureId,
                fixed_version_id: versionId !== 'none' ? versionId : undefined, // 
                assigned_to_id: data.assigned_to_id
            });
        }
        catch (error) {
            alert(`User Story: ${error instanceof Error ? error.message : 'Unknown error'}`);
            throw error; // 
        }
    };
    return (React.createElement(React.Fragment, null,
        React.createElement("div", { className: "user-story-grid" },
            storyIds.map(storyId => (React.createElement(UserStory, { key: storyId, storyId: storyId }))),
            React.createElement(AddButton, { type: "user-story", label: "+ Add User Story", dataAddButton: "user-story", epicId: epicId, featureId: featureId, versionId: versionId, onClick: handleAddUserStory })),
        React.createElement(IssueFormModal, { isOpen: isModalOpen, onClose: () => setIsModalOpen(false), onSubmit: handleSubmit, title: "\u65B0\u3057\u3044User Story\u3092\u8FFD\u52A0", subjectLabel: "User Story\u540D", subjectPlaceholder: "\u4F8B: \u30E6\u30FC\u30B6\u30FC\u304C\u30ED\u30B0\u30A4\u30F3\u3067\u304D\u308B", showAssignee: true, users: usersList })));
};
