import React, { useState } from 'react';
import { TestItem } from './TestItem';
import { AddButton } from '../common/AddButton';
import { IssueFormModal } from '../common/IssueFormModal';
import { useStore } from '../../store/useStore';
export const TestContainer = ({ userStoryId, testIds }) => {
    const createTest = useStore((state) => state.createTest);
    const users = useStore((state) => state.entities.users || {});
    const userStory = useStore((state) => state.entities.user_stories[userStoryId]);
    const [isModalOpen, setIsModalOpen] = useState(false);
    const usersList = Object.values(users);
    const handleAddTest = () => {
        setIsModalOpen(true);
    };
    const handleSubmit = async (data) => {
        try {
            await createTest(userStoryId, {
                subject: data.subject,
                description: data.description,
                parent_user_story_id: userStoryId,
                assigned_to_id: data.assigned_to_id
            });
        }
        catch (error) {
            alert(`Test: ${error instanceof Error ? error.message : 'Unknown error'}`);
            throw error; // 
        }
    };
    return (React.createElement(React.Fragment, null,
        React.createElement("div", { className: "test-container" },
            React.createElement("div", { className: "test-container-header" }, "Test"),
            React.createElement("div", { className: "test-item-grid" },
                testIds.map(testId => (React.createElement(TestItem, { key: testId, testId: testId }))),
                React.createElement(AddButton, { type: "test", label: "+ Add Test", dataAddButton: "test", className: "test-item", onClick: handleAddTest }))),
        React.createElement(IssueFormModal, { isOpen: isModalOpen, onClose: () => setIsModalOpen(false), onSubmit: handleSubmit, title: "\u65B0\u3057\u3044Test\u3092\u8FFD\u52A0", subjectLabel: "Test\u540D", subjectPlaceholder: "\u4F8B: \u30ED\u30B0\u30A4\u30F3\u6A5F\u80FD\u306E\u30C6\u30B9\u30C8", showAssignee: true, users: usersList, defaultAssigneeId: userStory?.assigned_to_id })));
};
